<p><span class="rvts58">变形学派（</span><span class="rvts59">Polymorph</span><span class="rvts58">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber282.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">变形学派一共有</span><span class="rvts20">19</span><span class="rvts21">个技能。变形学派的主要作用是利用大自然界一切生物的优缺点，使他们充分运用在与敌人的作战中。</span>
<br><span class="rvts21">该学派拥有一些可以进行跨越障碍地形的技能，还有一些额外的提供视野或对敌人施加减益</span><span class="rvts20">BUFF</span><span class="rvts21">的技能。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">变形学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">莱斯特医生（</span><span class="rvts20">Doctor Leste</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">艾特斯特（</span><span class="rvts20">Exter</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3. </span><span class="rvts21">欧维斯（</span><span class="rvts20">Ovis</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">4. </span><span class="rvts21">精灵荆棘舞者（</span><span class="rvts20">Elf Thorndancer</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">5. </span><span class="rvts21">黑环炼金师（</span><span class="rvts20">Black Ring Alchemist</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">6. </span><span class="rvts21">乔邦（</span><span class="rvts20">Jawbone</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">商人弗利尔（</span><span class="rvts20">Trader Friel</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
